body {
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  margin: 0;
  display: flex;
}

body .stopwatch {
  --minute-duration: 60s;
  border: 1vmin dotted #000;
  border-radius: 50%;
  width: 80vmin;
  height: 80vmin;
  position: relative;
}

body .stopwatch__dot, body .stopwatch__main-hand, body .stopwatch__second-hand {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

body .stopwatch__dot {
  background-color: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
}

body .stopwatch__main-hand {
  transform-origin: 50% 100%;
  animation: rotate-hand calc(var(--minute-duration) * 60) steps(60, end) infinite;
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  transform: translate(-50%, -100%);
}

body .stopwatch__second-hand {
  transform-origin: 50% 100%;
  animation: rotate-hand var(--minute-duration) linear infinite;
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  transform: translate(-50%, -100%);
}

body .stopwatch--speed-up {
  --minute-duration: 10s;
}

@keyframes rotate-hand {
  from {
    transform: translate(-50%, -100%)rotate(0);
  }

  to {
    transform: translate(-50%, -100%)rotate(360deg);
  }
}
